* {
  /* outline: 2px solid red; */
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins";
}

/* Hide unnecessary elements when printing */
@media print {
  body * {
    visibility: hidden;
  }
  .print-section,
  .print-section * {
    visibility: visible;
  }
  .print-section {
    position: absolute;
    top: 20%;
    left: 20%;
    /* transform: translate(-50%, -50%); */
  }
}

