.scroll-to-top,
.scroll-to-bottom,
.whatsapp-icon {
  position: fixed;
  right: 20px;
  background-color: #000;
  color: white;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  opacity: 0.7;
  z-index: 100000000;
}

.scroll-to-top:hover,
.scroll-to-bottom:hover,
.whatsapp-icon:hover {
  opacity: 1;
  transform: scale(1.1);
}

.whatsapp-icon {
  bottom: 140px;
  background-color: #15b450;
}

.scroll-to-top {
  bottom: 140px;
}

.scroll-to-bottom {
  bottom: 100px;
}

.back-top-container svg,
.back-bottom-container svg,
.whatsapp-container svg {
  fill: #fff;
}

@media (max-width: 768px) {
  .scroll-to-top,
  .scroll-to-bottom,
  .whatsapp-icon {
    width: 40px;
    height: 40px;
  }
}

