.nav-tabs {
  display: flex;
  justify-content: center;
  border-bottom: none;
  margin-bottom: 20px;
}

.nav-tabs .nav-item {
  margin: 0 10px;
}

.nav-tabs .nav-link {
  padding: 10px 20px;
  /* width: 160px; */
  border: 2px solid transparent;
  border-radius: 30px;
  background-color: #f8f9fa;
  font-weight: bold;
  font-size: 16px;
  color: #555;
  transition: all 0.3s ease;
  background-color: #ffebcc;
  color: #ff9800;
  border-color: #ff9800;
}

.nav-tabs .nav-link.active {
  background-color: #ff9800;
  border-color: #ff9800;
  color: white;
}

.nav-tabs .nav-link:hover {
  background-color: #ffebcc;
  color: #ff9800;
  border-color: #ff9800;
}

.nav-tabs .nav-link:focus {
  outline: none;
}

