.modal-overlay1 {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-content1 {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    width: 500px;
    position: relative;
  }
  
  .close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
  }
  

  
  .logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }
  

  
  .logo-text {
    font-size: 24px;
    font-weight: bold;
    color: #4CAF50;
  }
  
  p {
    /* text-align: center; */
    /* margin-bottom: 15px; */
  }
  
  .location-button {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px;
    border: 1px solid #4CAF50;
    border-radius: 4px;
    background-color: white;
    cursor: pointer;
  }
  
  .location-button.active {
    background-color: #E8F5E9;
  }
  
  .location-icon {
    margin-right: 10px;
  }
  
  .divider {
    text-align: center;
    margin: 15px 0;
    color: #888;
  }
  
  .location-input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }