/* Fullscreen Loader Container */
.loader-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(232, 239, 204, 0.7); /* Optional: Dim background */
    z-index: 9999;
  }
  
  /* Loader Spinner */
  .loaderAbh {
    width: 48px;
    height: 48px;
    background: #fff;
    border-radius: 50%;
    position: relative;
    animation: skLinRotate 1s ease-in-out infinite alternate;
  }
  
  .loaderAbh:after {
    content: "";
    position: absolute;
    inset: 5px;
    border-radius: 50%;
    border: 5px solid transparent;
    border-top-color: #ff3d00;
  }
  
  /* Keyframes for Rotation Animation */
  @keyframes skLinRotate {
    0% {
      transform: rotate(0deg);
    }
    95%, 100% {
      transform: rotate(840deg);
    }
  }
  