  position: absolute;
  top: 40px;
  left: 0;
  color: #000;
  padding: 10px 15px;
}
.bar-menu li {
  padding-top: 10px;
  padding-bottom: 10px;
}
@media screen and (max-width: 1200px) {
  .inside {
    visibility: hidden;
    display: none;
  }
  .pages:hover .inside {
    display: flex;
    flex-direction: column;
    visibility: visible;
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
  }
  .inside li {
    padding-top: 4px;
    color: #000;
  }
}
@media screen and (min-width: 1200px) {
  .bar-menu {
    display: flex;
    flex-direction: row;
    position: relative;
    top: -20px;
  }
  .pages:hover .inside {
    position: absolute;
  }
  .pages:hover .inside li {
    padding: 5px;
  }
}
@media screen and (width: 1200px) {
  .pages:hover .inside {
    position: absolute;
    top: 40px;
    left: 15px;
    padding: 10px 40px 10px 10px;
    width: fit-content;
    display: flex;
    flex-direction: column;
  }
  .pages:hover .inside li {
    padding: 5px;
    color: #000;
  }
}

